<template>
    <h5>D-day계산기 테스트중</h5>
    <div class="DDay">
        <h4 class="counter"></h4>
    </div>
  </template>
  
  <script>
  export default {
    method() {
        let counter = document.querySelector('.counter');
        let today = new Date(), //현재 날짜 가져오기
            dday = new Date("Dec 25,2022, 00:00:00").getTime(), //디데이
            gap = dday - today,
            result = Math.floor(gap / (1000 * 60 * 60 * 24));
        
        counter.innerHTML = `D-${result}`
    }
  }
  </script>
  
